@import url('https://fonts.googleapis.com/css?family=Oswald');

:local(link_item, link_item:hover, link_item:link, link_item:visited) {
    color: #000000;
    text-decoration: none;
}


body {
    margin: 0;
    padding: 0;
    font-family: Oswald;
}
header {
    height: 50px;
    width: 100%;
    background-color: #3c3b36;
}

main {
    display: flex;
    justify-content: center;
    width: 100%;
}
footer {
    height: 100px;
    background-color: #3c3b36;
}
#root {
    width: 65%;
    min-height: 1120px;
    border: 4px solid #000;
}

.logo {
    font-size: 26px;
    line-height: 50px;
    margin-left: 25px;

    color: #FFFFFF;
    float: left;
}

.auth-true {
    float: right;
}

.row {
    width: 68%;
    height: 1120px;
    position: relative;
    margin-right: 20px;
    float: right;
}
:local(.content) {
    display: flex;
    width: 100%;
    height: 94.5vh;
    flex-wrap: wrap;
    align-content: baseline;
    justify-content: center;
}

.item {
    width: 235px;
    margin-top: 20px;
    height: 305px;
    margin-left: 20px;
    background-color: #fff;
    font-size: 16px;
    font-weight: normal;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.head-item {
    height: 190px;
    overflow: hidden;
    display: flex;
    justify-items: center;
    align-items: center;
}

.bottom-item {
    height: 75px;
}

.title-item {
    height: 75px;
    overflow: hidden;
}

.price-item {
    display: inline-block;
    background-color: #d2ff75;
    height: 40px;
    width: 150px;
    float: left;
    overflow: hidden;
    line-height: 12px;
    font-size: 12px;
}

.buy {
    display: inline-block;
    background-color: #b6b6b6;
    height: 40px;
    width: 85px;
    float: left;
    text-indent: 15px;
    line-height: 40px;
    overflow: hidden;
}
.title {
    margin-top: 10px;
    text-indent: 10px;
    line-height: 20px;
    color: #000;
    text-decoration: none;
}
img {
    width: 85%;
    clear: none;
}
.img {
    width: 60%;
    padding-left: 23%;
    overflow: hidden;
}
.nosale {
    margin-left: 10px;
}

/*------------------------------------*/
/*NAVIGATION BAR*/
/*------------------------------------*/
div.navigation {
    width: 28%;
    display: inline-block;
    min-height: 1120px;
}
div.navigation-category {
    width: 100%;
    text-indent: 15px;
    height: 30px;
    margin-top: 10px;
    line-height: 30px;
    background-color: #37c6d6;
    color: #fdfbfb;
    font-weight: bolder;
    font-size: 19px;
}

a {
    text-decoration: none;
    color: #000;
}

.signup, .login, .logout, .username {
    color: #FFFFFF;
    cursor: pointer;
    font-size: 20px;
    float: right;
    text-align: center;
    line-height: 50px;
    margin-right: 20px;
}

.form-login {
    position: absolute;
    right: 10%;
    margin-top: 60px;
    z-index: 9999;
    margin-right: -135px;
    width: 275px;
    padding-left: 10px;
    height: 135px;
    background-color: #e7e7e7;

}

input {
    width: 87%;
    height: 30px;
    padding-left: 20px;
}

.form-group-login {
    padding-top: 10px;
}

.cart {
    width: 80%;
    display: flex;
}

/*#################################MESSAGE##########################################*/

.message-wrap {
    width: 100%;
    overflow: hidden;
    background-color: #f7f7f7;
    float: left;
}

.user-wrap {
    display: inline-block;
    margin-left: 10px;
    border: 1px solid #beffbb;
    text-align: center;
    height: 27px;
    width: 85px;
    margin-top: 12px;
}

.add-thread {
    width: 100%;
    height: 50px;
    border-bottom: 2px solid #b4b4b4;
}


.exist-message {
    width: 100%;
    overflow: auto;
    height: 93.5%;
}

.wrap1 {
    width: 60%;
    height: 100%;
    background-color: #FFFFFF;
}

.message-thread {
    float: left;
    height: 100%;
    width: 80%;
    display: flex;
    align-items: center;
    background-color: #f1f1f1;
    justify-content: center;

}

.message-dialog {
    padding-left: 15px;
    margin-bottom: 15px;
    height: 80px;
}

.sender {

}



.message-text {
    margin-top: 15px;
    background-color: #e4ecf1;

}

.thread:hover {
    background-color: #FFFFFF;
}


/*################################MESSAGE###########################################*/

.user-content {
    width: 20%;
    background: #22485d;
    color: #FFF;
    font-size: 24px;
    text-transform: uppercase;
}

.wrap {
    width: 100%;
    display: flex;
}

.inline {
    width: 100%;
    color: #fff;
    text-align: center;
    font-size: 16px;
}

.inline:link, .inline:focus {
    color: #FFFFFF;
}



.inline-actions:hover {
    background-color: #22485d;
}


.wrap-user {
    position: relative;
    margin-top: 60px;
}

.img-profile {
    position: relative;
    text-align: center;
}

.username-cart {
    position: relative;
    text-align: center;

}

.cart-content {
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.item-cart {
    width: 90%;
    height: 160px;
    display: flex;
    position: relative;
    background-color: #eff1f1;
    margin-top: 30px;
    font-size: 21px;
    border: 1px solid #cbd4d4;
    border-radius: 6px;
}

.price-item-cart {
    float: right;
    margin-top: 40px;
}

.center-cart {
    margin-top: 30px;
    margin-left: 35px;
    width: 60%;

}

.right-cart {
    width: 20%;
    float: right;
}

.img-item-cart {
    width: 12%;
    max-height: 130px;
    overflow: hidden;
    padding-left: 30px;
    padding-top: 10px;

}

.title-item-cart {

}

.count-item-cart {
    float: right;
    height: 40px;
    width: 40px;
    margin-top: 40px;
    margin-right: 45px;
    text-align: center;
}

button.button-buy {
    height: 50px;
    width: 120px;
    border: 2px solid #2a2a2a;
    font-size: 18px;
    margin-top: 100px;
    float: right;
    background: none;

}

button.button-buy:hover {
    background-color: #fdfbfb;
}

.close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
}

.close:hover {
    opacity: 1;
}

.close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #333;
}

.close:before {
    transform: rotate(45deg);
}

.close:after {
    transform: rotate(-45deg);
}


/*SPINNEEEEEEEEEEEEEEEEEEEEEEEEEEER*/
.cssload-thecube {
	width: 73px;
	height: 73px;
	margin: 0 auto;
	margin-top: 49px;
	position: relative;
	transform: rotateZ(45deg);
	-o-transform: rotateZ(45deg);
	-ms-transform: rotateZ(45deg);
	-webkit-transform: rotateZ(45deg);
	-moz-transform: rotateZ(45deg);
}

.cssload-thecube .cssload-cube {
	position: relative;
	transform: rotateZ(45deg);
	-o-transform: rotateZ(45deg);
	-ms-transform: rotateZ(45deg);
	-webkit-transform: rotateZ(45deg);
	-moz-transform: rotateZ(45deg);
}

.cssload-thecube .cssload-cube {
	float: left;
	width: 50%;
	height: 50%;
	position: relative;
	transform: scale(1.1);
	-o-transform: scale(1.1);
	-ms-transform: scale(1.1);
	-webkit-transform: scale(1.1);
	-moz-transform: scale(1.1);
}

.cssload-thecube .cssload-cube:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgb(43, 160, 199);
	animation: cssload-fold-thecube 2.76s infinite linear both;
	-o-animation: cssload-fold-thecube 2.76s infinite linear both;
	-ms-animation: cssload-fold-thecube 2.76s infinite linear both;
	-webkit-animation: cssload-fold-thecube 2.76s infinite linear both;
	-moz-animation: cssload-fold-thecube 2.76s infinite linear both;
	transform-origin: 100% 100%;
	-o-transform-origin: 100% 100%;
	-ms-transform-origin: 100% 100%;
	-webkit-transform-origin: 100% 100%;
	-moz-transform-origin: 100% 100%;
}

.cssload-thecube .cssload-c2 {
	transform: scale(1.1) rotateZ(90deg);
	-o-transform: scale(1.1) rotateZ(90deg);
	-ms-transform: scale(1.1) rotateZ(90deg);
	-webkit-transform: scale(1.1) rotateZ(90deg);
	-moz-transform: scale(1.1) rotateZ(90deg);
}

.cssload-thecube .cssload-c3 {
	transform: scale(1.1) rotateZ(180deg);
	-o-transform: scale(1.1) rotateZ(180deg);
	-ms-transform: scale(1.1) rotateZ(180deg);
	-webkit-transform: scale(1.1) rotateZ(180deg);
	-moz-transform: scale(1.1) rotateZ(180deg);
}

.cssload-thecube .cssload-c4 {
	transform: scale(1.1) rotateZ(270deg);
	-o-transform: scale(1.1) rotateZ(270deg);
	-ms-transform: scale(1.1) rotateZ(270deg);
	-webkit-transform: scale(1.1) rotateZ(270deg);
	-moz-transform: scale(1.1) rotateZ(270deg);
}

.cssload-thecube .cssload-c2:before {
	animation-delay: 0.35s;
	-o-animation-delay: 0.35s;
	-ms-animation-delay: 0.35s;
	-webkit-animation-delay: 0.35s;
	-moz-animation-delay: 0.35s;
}

.cssload-thecube .cssload-c3:before {
	animation-delay: 0.69s;
	-o-animation-delay: 0.69s;
	-ms-animation-delay: 0.69s;
	-webkit-animation-delay: 0.69s;
	-moz-animation-delay: 0.69s;
}

.cssload-thecube .cssload-c4:before {
	animation-delay: 1.04s;
	-o-animation-delay: 1.04s;
	-ms-animation-delay: 1.04s;
	-webkit-animation-delay: 1.04s;
	-moz-animation-delay: 1.04s;
}

@keyframes cssload-fold-thecube {
	0%, 10% {
		transform: perspective(136px) rotateX(-180deg);
		opacity: 0;
	}
	25%,
	75% {
		transform: perspective(136px) rotateX(0deg);
		opacity: 1;
	}
	90%,
	100% {
		transform: perspective(136px) rotateY(180deg);
		opacity: 0;
	}
}

@-o-keyframes cssload-fold-thecube {
	0%, 10% {
		-o-transform: perspective(136px) rotateX(-180deg);
		opacity: 0;
	}
	25%,
	75% {
		-o-transform: perspective(136px) rotateX(0deg);
		opacity: 1;
	}
	90%,
	100% {
		-o-transform: perspective(136px) rotateY(180deg);
		opacity: 0;
	}
}

@-ms-keyframes cssload-fold-thecube {
	0%, 10% {
		-ms-transform: perspective(136px) rotateX(-180deg);
		opacity: 0;
	}
	25%,
	75% {
		-ms-transform: perspective(136px) rotateX(0deg);
		opacity: 1;
	}
	90%,
	100% {
		-ms-transform: perspective(136px) rotateY(180deg);
		opacity: 0;
	}
}

@-webkit-keyframes cssload-fold-thecube {
	0%, 10% {
		-webkit-transform: perspective(136px) rotateX(-180deg);
		opacity: 0;
	}
	25%,
	75% {
		-webkit-transform: perspective(136px) rotateX(0deg);
		opacity: 1;
	}
	90%,
	100% {
		-webkit-transform: perspective(136px) rotateY(180deg);
		opacity: 0;
	}
}

@-moz-keyframes cssload-fold-thecube {
	0%, 10% {
		-moz-transform: perspective(136px) rotateX(-180deg);
		opacity: 0;
	}
	25%,
	75% {
		-moz-transform: perspective(136px) rotateX(0deg);
		opacity: 1;
	}
	90%,
	100% {
		-moz-transform: perspective(136px) rotateY(180deg);
		opacity: 0;
	}
}

.loading {
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #555752;
    opacity: 0.8;
    z-index: 9999;
}




